project img{height:50%;width:auto}}.project:nth-of-type(odd){flex-direction:row-reverse}@media screen and (max-width: 600px){.project{flex-direction:column}.project:nth-of-type(odd){flex-direction:column}}.project .project-content{max-width:400px;padding:32px;z-index:2;color:white}.project .project-content h1{margin:0 0 0 -2px;font-weight:400}.project .project-content h4{font-weight:500;font-size:16px;margin:0;opacity:0.5}.project .project-content h5{text-transform:uppercase;font-size:0px;font-weight:800;margin-bottom:-8px;letter-spacing:1px}.project .project-content h6{font-size:10px;font-weight:500;margin:-8px 0 16px 0}.project .illustration-hero{height:20%;width:auto}@media screen and (max-width: 600px){.project .illustration-hero{height:auto;width:90%}}.project .recall-hero{height:40%;width:auto}@media screen and (max-width: 600px){.project .recall-hero{height:auto;width:90%}}@media screen and (max-width: 600px){.project .fabric-hero.fabric-hero{height:auto;width:120%}}

